import React from "react";
import { Button, Typography, Grid, Paper } from "@material-ui/core";
import "./LearnMore.css";
import description from "../Components/images/Description.png";
import yourdata from "../Components/images/youremotions.png";
import ydata from "../Components/images/yourdata2.png";
function LearnMore() {
  return (
    <div className="signup">
      <Typography color="primary" variant="h4">
        Dreamer Buddy
      </Typography>
      <br />

      <Grid container justify="center">
        <Grid item xs={12}>
          <Grid container justify="center">
            <div className="intro">
              <Typography className="why" color="secondary" variant="h6">
                Dreamer Buddy  is an app for dreamers  by dreamers.  Dreamer
                Buddy takes dream input from users and returns data about their
                dreams. Dreamer Buddy Then compares your average dream to the
                global total.
              </Typography>
            </div>
          </Grid>
        </Grid>

        <Grid container justify="center">
          <Grid item>
            <br />
            <div className="report1">
              <Typography variant="h5" color="primary">
                First you Report a Dream
              </Typography>
            </div>
          </Grid>
          <Grid item xs={12}>
            <img className="moreimages" src={description} />
          </Grid>
          <Grid item xs={12}>
            <Grid container justify="center">
              <Grid item>
                <div className="summary1">
                  <Typography variant="h5" color="primary">
                    Get your dream summary
                  </Typography>
                </div>
              </Grid>
            </Grid>
          </Grid>
          <Grid item>
            <div className="summary1">
              <Typography variant="h5" color="primary">
                View your Data
              </Typography>
            </div>
          </Grid>
          <Grid item xs={12}>
            <img  className="moreimages" src={yourdata} />
          </Grid>
        </Grid>
      </Grid>
      <Grid container justify="center">
        <Grid item xs={12}>
          <img className="moreimages" src={ydata} />
        </Grid>
        <Grid item>
          <div className="summary1">
            <Typography variant="h5" color="primary">
              Explore
            </Typography>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default LearnMore;
